import React from "react";
import { Link } from "react-router-dom";
import brand from "../../../assests/img/online-portal.png";
import "../../../index.css"; // Make sure to import your CSS file
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import * as userAction from "../../../redux/user/user.action";
import * as userReducer from "../../../redux/user/user.reducer";
import * as userUtil from "../../../util/userUtil";
import * as orderReducer from '../../../redux/orders/order.reducer';

function Navbar() {
  let dispatch = useDispatch();
  let navigate = useNavigate();

  let userInfo = useSelector((state) => {
    return state[userReducer.userFeatureKey];
  });

  let orderInfo=useSelector((state)=>{

    return state[orderReducer.orderFeatureKey]
  });
  let { user} = userInfo;

  let{cartItmes}=orderInfo;

  let clickLogout = () => {
    dispatch(userAction.logoutUser(navigate));
  };

  let beforeLonginLinks = (
    <React.Fragment>
      <li className="nav-item">
        <Link to="/users/register" className="nav-link">
          <i className="bi bi-person-plus " /> Register
        </Link>
      </li>
      <li className="nav-item">
        <Link to="/users/login" className="nav-link">
          <i className="bi bi-box-arrow-in-right text-white" /> Login
        </Link>
      </li>
    </React.Fragment>
  );

  let afterLoginink = (
    <React.Fragment>
      <li className="nav-item">
        <Link to="/users/profile" className="nav-link">
          <img src={user.avatar} alt="" width="25" height="25" className="rounded-circle"/> {user.name}
        </Link>
      </li>
      <li className="nav-item">
        <Link to="/" className="nav-link" onClick={clickLogout}>
          <i className="fa fa-sign-out text-muted text-white" /> Logout
        </Link>
      </li>
    </React.Fragment>
  );

  return (
    <React.Fragment>
      <nav className="navbar navbar-expand-sm custom-navbar">
        <div className="container">
          <Link to="/" className="navbar-brand">
            <img
              src={brand}
              alt=""
              width="200"
              height="80"
              className="rounded-circle"
            />
          </Link>

          <div className="collapse navbar-collapse">
            <ul className="navbar-nav">
              <li className="nav-item">
                <Link to="/products/men" className="nav-link">
                  <i className="bi bi-person-badge" /> Men's Wear
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/products/kids" className="nav-link">
                  <i className="bi bi-baby" /> Kid's Wear
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/products/women" className="nav-link">
                  <i className="bi bi-person-circle" /> Women's Wear
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/products/upload" className="nav-link">
                  <i className="bi bi-cloud-upload" /> Upload
                </Link>
              </li>

              <li className="nav-item">
                <Link to="/orders/cart" className="nav-link">
                  <i className="fa fa-shopping-cart" />
                  <span className="badge badge-danger">{cartItmes.length}</span>
                </Link>
              </li>
            </ul>

            <ul className="navbar-nav ml-auto">
              {userUtil.isLoggedIn() ? afterLoginink : beforeLonginLinks}
            </ul>
          </div>
        </div>
      </nav>
    </React.Fragment>
  );
}

export default Navbar;
