import React from "react";
import * as productAction from '../../redux/product/product.action';
import brand from "../../assests/img/shop.jpeg";

import "animate.css";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";


function UploadProduct() {


  let dispatch=useDispatch();
  let navigate=useNavigate();



  const [product, setProduct] = useState({
    name: "",
    brand: "",
    price: "",
    qty: "",
    image: "",
    category: "",
    description: "",
    usage: "",
  });

  let updateProduct = (event) => {
    setProduct({ ...product, [event.target.name]: event.target.value });
  };

  //update image
  let updateImage = async (event) => {
    let imageFile = event.target.files[0];
    let base64Image = await convertBase64String(imageFile);
    setProduct({
      ...product,
      image: base64Image,
    });
  };

  let convertBase64String = (imageFile) => {
    return new Promise((resolve, reject) => {
      let fileReader = new FileReader();
      fileReader.readAsDataURL(imageFile);
      fileReader.addEventListener("load", () => {
        if (fileReader.result) {
          resolve(fileReader.result);
        } else {
          reject("Error Ocurred");
        }
      });
    });
  };


  let uploadProduct=(event)=>{

    event.preventDefault();
    dispatch(productAction.uploadProduct(product,navigate));

  }
  return (
    <React.Fragment>
      <section className="mens-wear-container">
        <div className="container">
          <div className="section-content">
            <div className="d-flex align-items-center">
              <div className="brand-logo">
                <img
                  className="rounded-circle"
                  src={brand}
                  alt=""
                  width="60"
                  height="50"
                />
              </div>
              <h3 className="section-title ml-3">Shopping-Application</h3>
            </div>
          </div>
        </div>
      </section>

      <section className="py-2 animate__animated animate__zoomIn">
        <div className="container">
          <div className="row">
            <div className="col-md-6 m-auto">
              <div className="card shadow">
                <div className="card-header bg-teal  text-white text-center py-3">
                  <h4>
                    <i className="bi bi-cloud-upload" /> Upload A Product
                  </h4>
                </div>
                <div className="card-body bg-light-blue">
                  <form onSubmit={uploadProduct}>
                    <div className="mb-3">
                      <label htmlFor="name" className="form-label">
                        Name
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        id="name"
                        name="name"
                        value={product.name}
                        onChange={updateProduct}
                        placeholder="Enter your name"
                        required
                      />
                    </div>
                    <div className="mb-3">
                      <label htmlFor="text" className="form-label">
                        Brand
                      </label>
                      <input
                        type="text"
                        name="brand"
                        value={product.brand}
                        onChange={updateProduct}
                        className="form-control"
                        id="brand"
                        placeholder="Enter your brand"
                        required
                      />
                    </div>

                    <div className="mb-3">
                      <label htmlFor="qty" className="form-label">
                        Qty
                      </label>
                      <input
                        type="number"
                        name="qty"
                        value={product.qty}
                        onChange={updateProduct}
                        className="form-control"
                        placeholder="Enter quantity"
                        required
                      />
                    </div>
                    <div className="mb-3">
                      <label htmlFor="price" className="form-label">
                        Price
                      </label>
                      <input
                        type="number"
                        name="price"
                        value={product.price}
                        onChange={updateProduct}
                        className="form-control"
                        placeholder="Enter price"
                        required
                      />
                    </div>
                    <div className="form-group">
                      <div className="custom-file">
                        <input
                          type="file"
                          onChange={updateImage}
                          className="custom-file-input"
                          id="customFile"
                          required
                        />
                        <label
                          className="custom-file-label"
                          htmlFor="customFile"
                        >
                          {product.image.length > 0 ? (
                            <img
                              src={product.image}
                              alt=""
                              width="20"
                              height="30"
                            />
                          ) : (
                            "Product Image"
                          )}
                        </label>
                      </div>
                    </div>
                    <div className="mb-3">
                      <label htmlFor="category" className="form-label">
                        Category
                      </label>
                      <select
                        name="category"
                        className="form-control"
                        value={product.category}
                        onChange={updateProduct}
                        required
                      >
                        <option value="">Select a category</option>
                        <option value="MEN">Men's Wear</option>
                        <option value="WOMEN">Women's Wear</option>
                        <option value="KIDS">Kid's Wear</option>
                      </select>
                    </div>
                    <div className="mb-3">
                      <label htmlFor="description" className="form-label">
                        Description
                      </label>
                      <textarea
                        name="description"
                        value={product.description}
                        onChange={updateProduct}
                        className="form-control"
                        placeholder="Enter product description"
                        required
                      />
                    </div>
                    <div className="mb-3">
                      <label htmlFor="usage" className="form-label">
                        Usage
                      </label>
                      <textarea
                        name="usage"
                        value={product.usage}
                        onChange={updateProduct}
                        className="form-control"
                        placeholder="Enter product usage"
                        required
                      />
                    </div>
                    <div className="d-grid">
                      <input
                        type="submit"
                        value="Upload "
                        className="btn btn-teal "
                      />
                    </div>
                  </form>
                </div>
                <div className="card-footer text-center py-3">
                  <img
                    src={brand}
                    className=" bg-teal rounded-circle"
                    alt="EventNow"
                    width="180"
                    height="40"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </React.Fragment>
  );
}

export default UploadProduct;
