import React from "react";
import brand from "../../assests/img/shop.jpeg";

import { Link } from "react-router-dom";

import * as productReducer from "../../redux/product/product.reducer";
import * as productAction from "../../redux/product/product.action";
import * as orderAction from '../../redux/orders/order.action';
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import { useEffect } from "react";
import Spinner from "../../layout/util/spinner/Spinner";

function MensWear() {
  let dispatch = useDispatch();

  useEffect(() => {
    dispatch(productAction.getMenProduct());
  }, []);
  //get product data from store

  let productInfo = useSelector((state) => {
    return state[productReducer.productFeatureKey];
  });

  let { loading, products } = productInfo;


  let clickAddToCart=(product)=>{
    const updatedProduct={...product,qty:1};
    dispatch(orderAction.addToCart(updatedProduct));


  }

  return (
    <React.Fragment>
      <section className="mens-wear-container">
        <div className="container">
          <div className="section-content">
            <div className="d-flex align-items-center">
              <div className="brand-logo">
                <img
                  className="rounded-circle"
                  src={brand}
                  alt=""
                  width="60"
                  height="30"
                />
              </div>
              <h3 className="section-title ml-3">Men's Wear</h3>
            </div>
          </div>
        </div>
      </section>

      {loading ? (
        <Spinner />
      ) : (
        <React.Fragment>
          {products.length > 0 ? (
            <React.Fragment>
              <section>
                <div className="container mt-3">
                  <div className="row">
                    {products.map((product) => {
                      return (
                        <div className="col-md-3" key={product._id}>
                          <div className="card product-card">
                            <div className="card-header bg-white">
                              <Link to={`/products/${product._id}`}>
                                <img
                                  src={product.image}
                                  alt=""
                                  className="img-fluid product-image"
                                />
                              </Link>
                            </div>
                            <div className="card-body">
                              <h5 className="font-weight-bold">
                                {product.name}
                              </h5>

                              <p className="product-price">{product.brand}</p>
                              <p className="product-price">
                                &#8377; {product.price}
                              </p>
                              <p className="product-description">
                                {product.description}
                              </p>
                              <Link to={"/orders/cart"}>
                                <button 
                                onClick ={clickAddToCart.bind(this,product)} className="btn btn-primary">
                                  Add to Cart
                                </button>
                              </Link>
                            </div>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>
              </section>
            </React.Fragment>
          ) : null}
        </React.Fragment>
      )}
    </React.Fragment>
  );
}

export default MensWear;
