import * as productAction from "../product/product.action";

export const productFeatureKey = "product-info";

let initialState = {
  loading: false,
  products: [],
  errorMessage: "",
  selectedProduct: {},
};

export const reducer = (state = initialState, action) => {
  let { type, payload } = action;
  switch (type) {
    case productAction.UPLOAD_PRODUCT_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case productAction.UPLOAD_PRODUCT_SUCCESS:
      return {
        ...state,
        loading: false,
      };
    case productAction.UPLOAD_PRODUCT_FAILURE:
      return {
        ...state,
        loading: false,
        errorMessage: payload,
      };

    case productAction.GET_MEN_PRODUCT_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case productAction.GET_MEN_PRODUCT_SUCCESS:
      return {
        ...state,
        loading: false,
        products: payload.products,
      };

    case productAction.GET_MEN_PRODUCT_FAILURE:
      return {
        ...state,
        loading: false,
        errorMessage: payload,
      };


      case productAction.GET_WOMEN_PRODUCT_REQUEST:
        return{

            ...state,
            loading:true
        }
        case productAction.GET_WOMEN_PRODUCT_SUCCESS:
            return{

                ...state,
                loading:false,
                products:payload.products
            }
        case productAction.GET_WOMEN_PRODUCT_FAILURE:
            return{

                ...state,
                loading:false,
                errorMessage:payload
            }


            case productAction.GET_KIDS_PRODUCT_REQUEST:
        return{

            ...state,
            loading:true
        }
        case productAction.GET_KIDS_PRODUCT_SUCCESS:
            return{

                ...state,
                loading:false,
                products:payload.products
            }
        case productAction.GET_KIDS_PRODUCT_FAILURE:
            return{

                ...state,
                loading:false,
                errorMessage:payload
            }

            case productAction.GET_PRODUCT_REQUEST:
        return{

            ...state,
            loading:true
        }
        case productAction.GET_PRODUCT_SUCCESS:
            return{

                ...state,
                loading:false,
                selectedProduct:payload.product
            }
        case productAction.GET_PRODUCT_FAILURE:
            return{

                ...state,
                loading:false,
                errorMessage:payload
            }



          
    default:
      return state;
  }
};
