import React from "react";
import brand from "../../assests/img/shop.jpeg";
import * as orderAction from "../../redux/orders/order.action";
import * as orderReducer from "../../redux/orders/order.reducer";
import { useSelector } from "react-redux";
import { useEffect } from "react";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";

function Cart() {
  let dispatch = useDispatch();
  let orderInfo = useSelector((state) => {
    return state[orderReducer.orderFeatureKey];
  });

  let { cartItmes } = orderInfo;

  useEffect(() => {}, []);

  let calcTotal = () => {
    let total = 0;
    for (let cartItem of cartItmes) {
      total += cartItem.qty * cartItem.price; // Use += to accumulate the total
    }
    return total;
  };
  

  let calTax = () => {
    return (calcTotal() * process.env.REACT_APP_TOTAL_TAX) / 100;
  };

  let grandTotal = () => {
    return calTax() + calcTotal();
  };

  let clickIncrItemQty = (productId) => {
    dispatch(orderAction.cartItemQtyIncr(productId));
  };

  let clickDcrItemQty = (productId) => {
    dispatch(orderAction.decrCartItemQty(productId));
  };

  const clickCartItemDelete = (productId) => {
    // Dispatch the DELETE_CART_ITEM action with itemId
    dispatch(orderAction.deleteCartItem(productId));
  };
  
  
  return (
    <React.Fragment>
      <section className="mens-wear-container">
        <div className="container">
          <div className="section-content">
            <div className="d-flex align-items-center">
              <div className="brand-logo">
                <img
                  className="rounded-circle"
                  src={brand}
                  alt=""
                  width="60"
                  height="50"
                />
              </div>
              <h3 className="section-title ml-3">Shopping-Application</h3>
            </div>
          </div>
        </div>
      </section>
      {cartItmes.length > 0 ? (
        <React.Fragment>
          <section className="container mt-3">
            <div className="row">
              <div className="col-md-8">
                <div className="card">
                  <div className="card-header bg-teal text-white">
                    <p className="h4">Your Cart Items</p>
                  </div>
                  <div className="card-body">
                    <table className="table table-hover text-center  table-striped">
                      <thead className="bg-teal text-white">
                        <tr>
                          <th>Image</th>
                          <th>Name</th>
                          <th>Quantity</th>
                          <th>Price</th>
                          <th>Action</th>
                        </tr>
                      </thead>
                      <tbody className="bg-brown text-black">
                        {cartItmes.map((cartItem, index) => {
                          // Set default quantity to 1 if not present or 0
                          const quantity = cartItem.qty || 1;

                          return (
                            <tr key={cartItem._id}>
                              <td>{index + 1}</td>
                              <td>
                                <img
                                  src={cartItem.image}
                                  alt=""
                                  width="40"
                                  height="45"
                                />
                              </td>

                              <td>
                                <li
                                  className="fa fa-minus-circle mx-1"
                                  onClick={clickDcrItemQty.bind(
                                    this,
                                    cartItem._id
                                  )}
                                />
                                {quantity}
                                <li
                                  className="fa fa-plus-circle mx-1"
                                  onClick={clickIncrItemQty.bind(
                                    this,
                                    cartItem._id
                                  )}
                                />
                              </td>
                              <td>&#8377;{cartItem.price}</td>
                              <td>
                                <button
                                  className="btn btn-danger btn-sm"
                                 
                                  onClick={() => clickCartItemDelete(cartItem._id)}
                                  
                                >
                                  Delete
                                </button>
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>

              <div className="col-md-4">
                <div className="card">
                  <div className="card-header bg-teal text-white">
                    <p className="h4">Your Total</p>
                  </div>
                  <div className="card-body">
                    <ul className="list-group">
                      <li className="list-group-item  ">
                        Total:
                        <span className="font-weight-bold">{calcTotal()}</span>
                      </li>

                      <li className="list-group-item ">
                        Tax:<span className="font-weight-bold">{calTax()}</span>
                      </li>

                      <li className="list-group-item ">
                        Grand Total:
                        <span className="font-weight-bold">{grandTotal()}</span>
                      </li>
                    </ul>
                    <Link
                      to="/orders/checkout"
                      className="btn btn-success btn-sm"
                    >
                      Chekout
                    </Link>
                    <Link to="/" className="btn btn-secondary btn-sm">
                      Shop More
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </React.Fragment>
      ) : (
        <React.Fragment>
          <div className="mt-4 text-center">
            <p className="h2 text-success">
              -----------Cart is Empty...........
            </p>
            <Link to={"/"} className="btn btn-danger btn-sm  ">
              Shop
            </Link>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  );
}

export default Cart;
