import React from "react";
import brand from "../../assests/img/shop.jpeg";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import * as userReducer from "../../redux/user/user.reducer";
import Spinner from "../../layout/util/spinner/Spinner";
import * as orderReducer from "../../redux/orders/order.reducer";
import * as orderAction from "../../redux/orders/order.action";

import stripeImage from "../../assests/img/online-portal.png";

import { useNavigate } from "react-router-dom";
import StripeCheckout from "react-stripe-checkout";

function CheckOut() {
  let dispatch = useDispatch();
  let navigate = useNavigate();
  let userInfo = useSelector((state) => {
    return state[userReducer.userFeatureKey];
  });

  let cartItemInfo = useSelector((state) => {
    return state[orderReducer.orderFeatureKey];
  });

  let { cartItmes } = cartItemInfo;

  let { loading, user } = userInfo;

  let calcTotal = () => {
    let total = 0;
    for (let cartItem of cartItmes) {
      total += cartItem.qty * cartItem.price; // Use += to accumulate the total
    }
    return total;
  };

  let calTax = () => {
    return (calcTotal() * 5) / 100;
  };

  let grandTotal = () => {
    return calTax() + calcTotal();
  };

  let clickPayment = (token) => {
   
    let product = {
       price: Number(grandTotal()) * 100,
     name: "Products from shop",
     };
    let body = {
       token,
       product
      
    };
   dispatch(orderAction.makeStripePayment(body, navigate));
    console.log(token);
  };

  return (
    <React.Fragment>
      <section className="mens-wear-container">
        <div className="container">
          <div className="section-content">
            <div className="d-flex align-items-center">
              <div className="brand-logo">
                <img
                  className="rounded-circle"
                  src={brand}
                  alt=""
                  width="60"
                  height="50"
                />
              </div>
              <i className="fa fa-check-circle ml-4" />{" "}
              <h3 className="section-title  ml-3">CheckOut</h3>
            </div>
          </div>
        </div>
      </section>

      {loading ? (
        <Spinner />
      ) : (
        <React.Fragment>
          {Object.keys(user).length > 0 && (
            <section>
              <div className="container">
                <div className="row">
                  <div className="py-3 col-md-8">
                    <div className="card">
                      <div className="card-header  bg-teal text-white">
                        <p className="h4">
                          Billing Address{" "}
                          <Link
                            to="/users/profile"
                            className="btn btn-primary btn-sm  float-right"
                          >
                            Update Address
                          </Link>
                        </p>
                      </div>
                      <div className="card-body bg-dark">
                        <ul className="list-group bg-dark">
                          <li className="list-group-item text-black">
                            <small>
                              <strong className="font-weight-bold">
                                S.no: &nbsp; &nbsp;
                              </strong>
                              <span className="text-green">
                                {" "}
                                {user?.address.flat}
                              </span>
                            </small>
                            <br />
                            <small>
                              <strong className="font-weight-bold">
                                Street:&nbsp; &nbsp;
                              </strong>
                              <span className="text-green">
                                {" "}
                                {user?.address.street}
                              </span>
                            </small>
                            <br />
                            <small>
                              <strong className="font-weight-bold">
                                Landmark:&nbsp; &nbsp;
                              </strong>
                              <span className="text-green">
                                {" "}
                                {user?.address.landmark}
                              </span>
                            </small>
                            <br />
                            <small>
                              <strong className="font-weight-bold">
                                City: &nbsp; &nbsp;
                              </strong>
                              <span className="text-green">
                                {" "}
                                {user?.address.city}
                              </span>
                            </small>
                            <br />
                            <small>
                              <strong className="font-weight-bold">
                                State: &nbsp; &nbsp;
                              </strong>{" "}
                              <span className="text-green">
                                {" "}
                                {user?.address.state}
                              </span>
                            </small>
                            <br />
                            <small>
                              <strong className="font-weight-bold">
                                Country: &nbsp; &nbsp;
                              </strong>{" "}
                              <span className="text-green">
                                {" "}
                                {user?.address.country}
                              </span>
                            </small>
                            <br />
                            <small>
                              <strong className="font-weight-bold">
                                Pass code: &nbsp; &nbsp;
                              </strong>
                              <span className="text-green">
                                {" "}
                                {user?.address.passcode}
                              </span>
                            </small>
                            <br />
                            <small>
                              <strong className="font-weight-bold">
                                Mobile: &nbsp; &nbsp;
                              </strong>{" "}
                              <span className="text-green">
                                {" "}
                                {user?.address.mobile}
                              </span>
                            </small>
                            <br />
                          </li>
                        </ul>
                      </div>
                    </div>
                    <pre>{process.env.REACT_APP_STRIPE_PUBLIC_KEY}</pre>
                    <div className="card mt-3">
                      <div className="card-header bg-teal text-white">
                        <p className="h4">Payment Details</p>
                      </div>
                      <div className="card-body">
                        <form>
                          <div className="form-check">
                            <input
                              className="form-check-input"
                              type="radio"
                              name="flexRadioDefault"
                              id="flexRadioDefault1"
                            />
                            <label
                              className="form-check-label"
                              htmlFor="flexRadioDefault1"
                            >
                              Cash on Delivery
                            </label>
                          </div>
                          <div className="form-check">
                            <input
                              className="form-check-input"
                              type="radio"
                              name="flexRadioDefault"
                              id="flexRadioDefault2"
                            />
                            <label
                              className="form-check-label"
                              htmlFor="flexRadioDefault2"
                            >
                              Credit Card
                            </label>
                          </div>
                        </form>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="card">
                      <div className="card-header bg-teal text-white">
                        <p className="h4">Your Cart</p>
                      </div>
                      <div className="card-body bg-dark">
                        {cartItmes.length > 0 && (
                          <ul className="list-group">
                            {cartItmes.map((cartItem) => {
                              return (
                                <li
                                  key={cartItem._id}
                                  className="list-group-item"
                                >
                                  <div className="row">
                                    <div className="col-md-3">
                                      {
                                        <img
                                          src={cartItem.image}
                                          alt=""
                                          style={{ width: "50px" }}
                                        />
                                      }
                                    </div>
                                    <div className="col-md-7">
                                      <small>
                                        <strong className="font-weight-bold">
                                          {cartItem.name}
                                        </strong>
                                      </small>
                                      <br />
                                      <small>&#8377; {cartItem.price}</small>
                                      <br />
                                      <small>
                                        <strong>Qty:</strong> {cartItem.qty}
                                      </small>
                                    </div>
                                  </div>
                                </li>
                              );
                            })}
                          </ul>
                        )}

                        <ul className="list-group mt-3">
                          <li className="list-group-item">
                            <strong>Total:</strong>
                            <span className="font-weight-bold">
                              {calcTotal()}
                            </span>
                          </li>
                          <li className="list-group-item">
                            <strong>Tax:</strong>
                            <span className="font-weight-bold">{calTax()}</span>
                          </li>
                          <li className="list-group-item">
                            <strong>Grand Total:</strong>
                            <span className="font-weight-bold">
                              {grandTotal()}
                            </span>
                          </li>
                        </ul>

                        <StripeCheckout
                          token={clickPayment}
                          stripeKey={process.env.REACT_APP_STRIPE_PUBLIC_KEY}
                          name="Stripe Payment"
                          amount={grandTotal() * 100} // Multiply by 100 for cents
                          description="Payment with stripe"
                          currency="inr" // Use 'INR' for Indian Rupees
                          image={stripeImage}
                        >
                          <button className="btn btn-danger btn-sm btn-block mt-3">
                            PAY &#8377; {grandTotal()}
                          </button>
                        </StripeCheckout>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          )}
        </React.Fragment>
      )}
    </React.Fragment>
  );
}

export default CheckOut;
