import React from 'react';
import { Link } from 'react-router-dom';

function Home() {


  return (
    <div className="landing-page">
      <div className="wrapper">
        <div className="d-flex">
          <div className="circle-background">
            <div className="circle-image"></div>
          </div>
          <div className="text-container">
            <h5 className="display-4">Shopping-Portal</h5>
            <div className="text-border">
              
              <p className="lead px-4">
                EVENTSNOW – ALL UPCOMING EVENT <br />
                Find all upcoming Academic Events, International Conferences,
                Virtual Meetings, Webinars, Concert, Festival, Video Conferences,
                Events near me, Web Conferences, Live Show etc. | Submit Your
                Eventsnow for free and get a large number of attendees for your
                event to make it a grand success
              </p>
              
            </div>
            <Link to="/events/free" className="btn btn-danger btn-sm">
              Shop Now
            </Link>
          </div>
        </div>
      </div>
    </div>
  );

}

export default Home;
