import * as orderAction from "./order.action";

export const orderFeatureKey = "Order-info";

let initialState = {
  loading: false,
  cartItmes: [
    {
      _id: "64e49edaa70006db764ee4f8",
      name: "Men fit t-shirt",
      brand: "US POLO",
      price: 230,
      qty: 1,
      image:
        "data:image/jpeg;base64,",
      category: "MEN",
      description:
        "Mens Wear- Speak your styleBuying Guides in Men's Wear. ... Men's clothing though is basically structured around the pants and shirts but still they can be categorized in to formal wear, business wear, casual wear and athletic/sport wear which are divided on the basis of the occasion they are worn on.",
      usage:
        "Mens Wear- Speak your styleBuying Guides in Men's Wear. ... Men's clothing though is basically structured around the pants and shirts but still they can be categorized in to formal wear, business wear, casual wear and athletic/sport wear which are divided on the basis of the occasion they are worn on.",
      createdAt: "2023-08-22T11:41:14.091Z",
      updatedAt: "2023-08-22T11:41:14.091Z",
      __v: 0,
    },
  ],
  errrMessage: "",
  order:{}
};

export const reducer = (state = initialState, action) => {
  let { type, payload } = action;

  switch (type) {
    case orderAction.ADD_TO_CART:
      const existingItem = state.cartItmes.find(
        (item) => item._id === payload.item._id
      );
      if (!existingItem) {
        // Item not present in cart, add it
        return {
          ...state,
          cartItmes: [...state.cartItmes, payload.item],
        };
      } else {
        // Item already in cart, update quantity
        const updatedCartItems = state.cartItmes.map((item) => {
          if (item._id === payload.item._id) {
            return {
              ...item,
              qty: item.qty + payload.item.qty,
            };
          }
          return item;
        });
        return {
          ...state,
          cartItmes: updatedCartItems,
        };
      }

    case orderAction.ADD_TO_CART_FAILURE:
      return {
        ...state,
        errrMessage: payload,
      };
    case orderAction.INCR_CART_ITEM_QTY:
      let incrementCartItem = state.cartItmes.map((cartItem) => {
        if (cartItem._id === payload.productId) {
          return {
            ...cartItem,
            qty: cartItem.qty + 1,
          };
        }
        return cartItem;
      });


      return {
       ...state,
        cartItmes: [...incrementCartItem],
      };

      
    case orderAction.INCR_CART_ITEM_QTY_FAILURE:
      return {
        ...state,
        loading: false,
        errrMessage: payload,
      };

    case orderAction.DECR_CART_ITEM_QTY:
      let decrCartItem = state.cartItmes.map((cartItem) => {
        if (cartItem._id === payload.productId) {
          return {
            ...cartItem,
            qty: cartItem.qty - 1 > 0 ? cartItem.qty - 1 : 1,
          };
        }
        return cartItem;
      });
      return {
        ...state,
        cartItmes: [...decrCartItem],
      };

    case orderAction.DECR_CART_ITEM_QTY_FAILURE:
      return {
        ...state,
      
        errrMessage: payload,
      };

      case orderAction.DELETE_CART_ITEM:
        
      let deleteCartItems=state.cartItmes.filter(cartItem=>{

        return cartItem._id!==payload.productId;
      });

      return {

        ...state,
        cartItmes:[...deleteCartItems]
      }
    case orderAction.DELETE_CART_ITEM_FAILURE:
      return {
        ...state,
        errrMessage: payload,
      };



      //make stripe payments

       case orderAction.STRIPE_PAYMENT_REQUEST:
        return{

          ...state,
          loading:true
        }


        case orderAction.STRIPE_PAYMENT_SUCCESS:
            return{


                ...state,
                loading:false
            }
          case orderAction.STRIPE_PAYMENT_FAILURE:
            return{

              ...state,
              loading:false,
              errrMessage:payload
            }


          case orderAction.PLACE_ORDER_REQUEST:
            return{

              ...state,
              loading:true,

            }
           case orderAction.PLACE_ORDER_SUCCESS:
            return{

              ...state,
              loading:false,
              order:payload.order

            }
          case orderAction.PLACE_ORDER_FAILURE:
            return{

              ...state,
              loading:false,
        
              errrMessage:payload

            }
    default:
      return state;
  }
};
