import React from "react";
import * as alertReducer from "../../../redux/alert/alert.reducer";
import { useSelector } from "react-redux";


function Alert() {
  let alertInfo = useSelector((state) => {
    return state[alertReducer.alertFeatureKey];
  });

  let { alertMessage } = alertInfo;

  return (
    <React.Fragment>
      {alertMessage.length > 0 ? (
        <React.Fragment>
          <div
            className={`alert alert-${alertMessage[0].color} alert-dismissible fixed-top m-2 animated  slideInDown`}
          >
            <button className="close">
              <i className="fa fa-times-circle" />
            </button>
            {alertMessage.map((alert) => (
              <div key={alert.id}>
                <small className="font-weight-bold">{alert.message}</small>
              </div>
            ))}
          </div>
        </React.Fragment>
      ) : null}
    </React.Fragment>
  );
}

export default Alert;
