//to find user loggedin or not

export const isLoggedIn=()=>{

    if(localStorage.getItem(process.env.REACT_APP_LOGIN_TOKEN)) //if token is available or not
    {

        return true;
    }
    else
    return false;
}
//to find the token

export const getToken=()=>{

    return localStorage.getItem(process.env.REACT_APP_LOGIN_TOKEN);
}