import React, { useEffect, useState } from "react";
import brand from "../../assests/img/shop.jpeg";
import * as userAction from '../../redux/user/user.action';

import { useDispatch, useSelector } from "react-redux";
import * as userReducer from "../../redux/user/user.reducer";
import Spinner from "../../layout/util/spinner/Spinner";

function UserProfile() {
  let [enableAddress, setEnableAddress] = useState(false);
  let dispatch=useDispatch();
  

  let userInfo = useSelector((state) => {
    return state[userReducer.userFeatureKey];
  });

  let { loading, user } = userInfo;

  let [address,setAddress]=useState({


      flat:'',
      street:'',
      landmark:'',
      city:'',
      state:'',
      country:'',
      pin:'',
      mobile:''
  });

  useEffect(()=>{

    setAddress({

      flat:user && user.address ?user.address.flat:'',
      street:user && user.address ?user.address.street:'',
      landmark:user && user.address?user.address.landmark:'',
      city:user && user.address?user.address.city:'',
      state:user && user.address?user.address.state:'',
      country:user && user.address?user.address.country:'',
      passcode:user && user.address ?user.address.passcode:'',
      mobile:user && user.address ?user.address.mobile:''
    

    })
  },[user]);

  let updateInputAddress=(e)=>{

    setAddress({

      ...address,
      [e.target.name]:e.target.value

    });
    

    
  }

  let submitUpdateAddress=(e)=>{
    e.preventDefault();

     dispatch(userAction.updateAddress(address));
   
   
    
    setEnableAddress(false);


  }

  return (
    <React.Fragment>
      <section className="mens-wear-container">
        <div className="container">
          <div className="section-content">
            <div className="d-flex align-items-center">
              <div className="brand-logo">
                <img
                  className="rounded-circle"
                  src={brand}
                  alt=""
                  width="60"
                  height="50"
                />
              </div>
              <h3 className="section-title ml-3">Shopping-Application</h3>
            </div>
          </div>
        </div>
      </section>

      {loading ? (
        <Spinner />
      ) : (
        <React.Fragment>
          {Object.keys(user).length > 0 && (
            <section>
              <div className="container mt-3">
                <div className="row">
                  <div className="col-md-3">
                    <img
                      src={user.avatar}
                      alt=""
                      className="img-fluid rounded-circle profile-image"
                      style={{ width: "120px", height: "120px" }}
                    />
                  </div>
                  <div className="col-md-9">
                    <div className="card">
                      <div className="card-header bg-teal text-white">
                        <h3 className="card-title">Your Information</h3>
                      </div>
                      <div className="card-body bg-dark">
                        <ul className="list-group">
                          <li className="list-group-item">
                            <strong>Name:</strong> {user.name}
                          </li>
                          <li className="list-group-item">
                            <strong>Email:</strong> {user.email}
                          </li>
                          <li className="list-group-item">
                            <strong>Mobile:</strong> {user.address.mobile}
                          </li>
                        </ul>
                      </div>
                    </div>

                    <div className="card  bg-light-blue mt-3">
                      <div className="card-header bg-teal text-white">
                        <h4 className="card-title">Billing Address</h4>
                        <div className="custom-control custom-switch float-right">
                          <input
                            onChange={(e) => setEnableAddress(e.target.checked)}
                            type="checkbox"
                            className="custom-control-input"
                            id="customSwitch1"
                          />
                          <label
                            className="custom-control-label"
                            htmlFor="customSwitch1"
                          >
                            Enable Address
                          </label>
                        </div>
                      </div>
                      {user.address && !enableAddress && (
                        <div className="card-body bg-black">
                          <ul className="list-group">
                            <li className="list-group-item">
                              Flat:{" "}
                              <span className="font-weight-bold">
                                {user.address.flat}
                              </span>{" "}
                            </li>
                            <li className="list-group-item">
                              Street:{" "}
                              <span className="font-weight-bold">
                                {user.address.street}
                              </span>
                            </li>
                            <li className="list-group-item">
                              LandMark:{" "}
                              <span className="font-weight-bold">
                                {user.address.landmark}
                              </span>
                            </li>
                            <li className="list-group-item">
                              City:{" "}
                              <span className="font-weight-bold">
                                {user.address.city}
                              </span>
                            </li>
                            <li className="list-group-item">
                              State:{" "}
                              <span className="font-weight-bold">
                                {user.address.state}
                              </span>
                            </li>
                            <li className="list-group-item">
                              Country:{" "}
                              <span className="font-weight-bold">
                                {user.address.country}
                              </span>
                            </li>
                            <li className="list-group-item">
                              Passcode:{" "}
                              <span className="font-weight-bold">
                                {user.address.passcode}
                              </span>
                            </li>
                            <li className="list-group-item">
                              Mobile:{" "}
                              <span className="font-weight-bold">
                                {user.address.mobile}
                              </span>
                            </li>
                          </ul>
                        </div>
                      )}

                      {user.address && enableAddress && (
                        <div className="card-body">
                          <form onSubmit={submitUpdateAddress}>
                            
                            <div class="input-group mb-3">
                              <span class="input-group-text text-brown"  id="basic-addon1">
                                Flat
                              </span>
                              <input
                               name='flat'
                               value={address.flat}
                               onChange={updateInputAddress}
                              type="text" class="form-control" />
                            </div>

                            <div class="input-group mb-3">
                              <span class="input-group-text" id="basic-addon1">
                                Street
                              </span>
                              <input
                               name='street'
                               value={address.street}
                               onChange={updateInputAddress} type="text" class="form-control" />
                            </div>

                            <div class="input-group mb-3">
                              <span class="input-group-text" id="basic-addon1">
                                LandMark
                              </span>
                              <input
                               name='landmark'
                               value={address.landmark}
                               onChange={updateInputAddress} type="text" class="form-control" />
                            </div>

                            <div class="input-group mb-3">
                              <span class="input-group-text" id="basic-addon1">
                                City
                              </span>
                              <input
                               name='city'
                               value={address.city}
                               onChange={updateInputAddress}
                              type="text" class="form-control" />
                            </div>

                            <div class="input-group mb-3">
                              <span class="input-group-text" id="basic-addon1">
                                State
                              </span>
                              <input 
                               name='state'
                               value={address.state}
                               onChange={updateInputAddress}type="text" class="form-control" />
                            </div>

                            <div class="input-group mb-3">
                              <span class="input-group-text" id="basic-addon1">
                                Country
                              </span>
                              <input 
                               name='country'
                               value={address.country}
                               onChange={updateInputAddress}type="text" class="form-control" />
                            </div>

                            <div class="input-group mb-3">
                              <span class="input-group-text" id="basic-addon1">
                                Passcode
                              </span>
                              <input 
                               name='passcode'
                               value={address.passcode}
                               onChange={updateInputAddress}
                              type="text" class="form-control" />
                            </div>

                            <div class="input-group mb-3">
                              <span class="input-group-text" id="basic-addon1">
                                Mobile
                              </span>
                              <input
                               name='mobile'
                               value={address.mobile}
                               onChange={updateInputAddress}
                              type="text" class="form-control" />
                            </div>

                            <div className="d-grid">
                              <input
                                type="submit"
                                value="Upload "
                                className="btn btn-teal "
                              />
                            </div>
                          </form>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </section>
          )}
        </React.Fragment>
      )}
    </React.Fragment>
  );
}

export default UserProfile;
