import React, { useEffect } from "react";
import brand from "../../assests/img/shop.jpeg";
import { useParams } from "react-router-dom";
import * as productAction from "../../redux/product/product.action";
import * as productReducer from "../../redux/product/product.reducer";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import Spinner from "../../layout/util/spinner/Spinner";
import { useState } from "react";
import * as orderAction from '../../redux/orders/order.action';
import { useNavigate } from "react-router-dom";

function ProductDetails() {
  
  let dispatch = useDispatch();
  let productId = useParams().productId;

  let [selectedQty, setSelectedQty] = useState('');
  let navigate=useNavigate();

  useEffect(() => {
    dispatch(productAction.getProduct(productId));
  }, [productId]);

  let selectedProductInfo = useSelector((state) => {
    return state[productReducer.productFeatureKey];
  });

  let { loading, selectedProduct } = selectedProductInfo;


  let handleAddToCart=(event)=>{

    event.preventDefault();
    const updatedProduct = {
      ...selectedProduct,
      qty: Number(selectedQty)
    };
    dispatch(orderAction.addToCart(updatedProduct,navigate));
   
  }

 


  return (
    <React.Fragment>
      <section className="mens-wear-container">
        <div className="container">
          <div className="section-content">
            <div className="d-flex align-items-center">
              <div className="brand-logo">
                <img
                  className="rounded-circle"
                  src={brand}
                  alt=""
                  width="60"
                  height="50"
                />
              </div>
              <h3 className="section-title ml-3">Your Selected Product</h3>
            </div>
          </div>
        </div>
      </section>

      {loading ? (
        <Spinner />
      ) : (
        <React.Fragment>
          <section>
            <div className="container">
              <div className="row">
                {Object.keys(selectedProduct).length > 0 && (
                  <React.Fragment>
                    <div className="col-md-4 py-4">
                      <img
                        src={selectedProduct.image}
                        alt=""
                        className="img-fluid product-image"
                      />
                    </div>
                    <div className="col-md-8 py-2 product-info">
                      <h2>{selectedProduct.name}</h2>
                      <p>Brand: {selectedProduct.brand}</p>
                      <p className="product-price">
                        Price: &#8377;{selectedProduct.price}
                      </p>
                      <p>Description: {selectedProduct.description}</p>
                      <pre>{selectedQty}</pre>

                      <form onSubmit={handleAddToCart}>
                        <select
                          value={selectedQty}
                          onChange={(e) => setSelectedQty(e.target.value)}
                          className="form-select product-select"
                        >
                          <option value="">Select a Qty</option>
                          <option value="1">1</option>
                          <option value="2">2</option>
                          <option value="3">3</option>
                          <option value="4">4</option>
                        </select>
                        <button type="submit" className="btn btn-primary">
                          Add to Cart
                        </button>
                      </form>
                    </div>
                  </React.Fragment>
                )}
              </div>
            </div>
          </section>
        </React.Fragment>
      )}
    </React.Fragment>
  );
}

export default ProductDetails;
